import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { Row, Col, Image, ListGroup, Card, Form, Button } from 'react-bootstrap'
import Rating from '../components/Rating'
import { useDispatch, useSelector } from 'react-redux'
import { listProductDetails } from '../actions/productActions'
import Loader from '../components/Loader'
import Message from '../components/Message'

const ProductScreen = ({ history, match }) => {
  const [qty, setQty] = useState(1)
  const dispatch = useDispatch()


  useEffect(() => {
    dispatch(listProductDetails(match.params.id))

  }, [dispatch, match])

  const addToCartHandler = () => {
    history.push(`/cart/${match.params.id}?qty=${qty}`)
  }

  const userLogin = useSelector(state => state.userLogin)
  const { userInfo } = userLogin

  const productDetails = useSelector(state => state.productDetails)
  const { product, loading, error } = productDetails

  return (
    <>
      <Link className="btn btn-light my-3" to="/">
        {' '}
        Go Back{' '}
      </Link>
      {loading ? (<Loader />) : error ? (<Message variant='danger' children={error} />) :
        (<Row>
          <Col md={6}>
            <Image src={product.image} alt={product.name} fluid />
          </Col>
          <Col md={3}>
            <ListGroup variant="flush">
              <ListGroup.Item>
                <h3>{product.name}</h3>
              </ListGroup.Item>
              <ListGroup.Item>
                <Rating
                  value={product.rating}
                  text={`${product.numReviews} reviews`}
                  color="#FDCC0D"
                />
              </ListGroup.Item>
              <ListGroup.Item>price : â‚¹{product.price}</ListGroup.Item>
              <ListGroup.Item>price : {product.description}</ListGroup.Item>
            </ListGroup>
          </Col>
          <Col md={3}>
            <Card>
              <ListGroup variant="flush">
                <ListGroup.Item>
                  <Row>
                    <Col>Price:</Col>
                    <Col>
                      <strong>â‚¹{product.price}</strong>
                    </Col>
                  </Row>
                </ListGroup.Item>
                <ListGroup.Item>
                  <Row>
                    <Col>Status</Col>
                    <Col>
                      <strong>
                        {product.countInStock > 0 ? 'In Stock' : 'Out Of Stock'}
                      </strong>
                    </Col>
                  </Row>
                </ListGroup.Item>
                {product.countInStock > 0 && (
                  <ListGroup.Item>
                    <Row>
                      <Col> Qty </Col>
                      <Col>
                        <Form.Control as='select' value={qty} onChange={(e) => setQty(e.target.value)}>
                          {
                            [...Array(product.countInStock).keys()].map((x) => (
                              <option key={x + 1} value={x + 1}>{x + 1}</option>
                            ))
                          }
                        </Form.Control>
                      </Col>
                    </Row>
                  </ListGroup.Item>
                )}
                <ListGroup.Item>
                  {userInfo ? (

                    <div className="d-grid gap-1">
                      <Button
                        onClick={addToCartHandler}
                        variant='info'
                        size='lg'
                        disabled={product.countInStock === 0}

                      >
                        Add To cart
                      </Button>
                    </div>) : (
                    <>
                      <Link to='/login'>
                        <div className="d-grid">
                          <Button variant="dark" size="lg">
                            Add To Cart
                          </Button>
                        </div>
                      </Link>
                    </>
                  )}


                </ListGroup.Item>
              </ListGroup>
            </Card>
          </Col>
        </Row>)}

    </>
  )
}

export default ProductScreen
