import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import Message from '../components/Message'
import { Link } from 'react-router-dom'
import { Row, Col, Form, Button, ListGroup, Image, Card, Jumbotron, Container } from 'react-bootstrap'
import { addToCart, removeFromCart } from '../actions/cartActions'
import CheckoutSteps from '../components/CheckoutSteps'


const CartScreen = ({ match, location, history }) => {

    const productId = match.params.id

    const qty = location.search ? Number(location.search.split("=")[1]) : 1
    const dispatch = useDispatch()

    const cart = useSelector(state => state.cart)
    const { cartItems } = cart

    const removeFromCartHandler = (id) => {
        dispatch(removeFromCart(id))
    }

    const checkoutHandler = () => {
        history.push('/login?redirect=shipping')
    }

    useEffect(() => {
        if (productId) {
            dispatch(addToCart(productId, qty))
        }
    }, [dispatch, productId, qty])

    return (
        <Row>
            <h1>Shopping Cart</h1>
            <CheckoutSteps step1 />
            {cartItems.length === 0 ? (
                <>
                    <Message variant='danger'>
                        <Link className="lead" to="/">
                            Go back
                        </Link>
                    </Message>
                    <Jumbotron>
                        <Container className="text-center">
                            <i className="fa fa-shopping-cart fa-7x"></i>
                            <h1 className="display-3 ">Your cart is empty !</h1>
                            <hr className="my-2" />
                            <p className="lead">Start Adding stuffs to it Explore now. Click <Link to="/">go back</Link> </p>
                        </Container>

                    </Jumbotron>
                </>
            ) : (
                <Row>
                    <Col md={8}>
                        <ListGroup variant='flush'>
                            {cartItems.map((item) => (
                                <ListGroup.Item key={item.product}>
                                    <Row>
                                        <Col md={2}>
                                            <Image src={item.image} alt={item.name} fluid rounded />
                                        </Col>
                                        <Col md={3}>
                                            <Link to={`/product/${item.product}`}>{item.name}</Link>
                                        </Col>
                                        <Col md={2}>â‚¹{item.price}</Col>
                                        <Col md={2}>
                                            <Form.Control
                                                as='select'
                                                value={item.qty}
                                                onChange={(e) =>
                                                    dispatch(
                                                        addToCart(item.product, Number(e.target.value))
                                                    )
                                                }
                                            >
                                                {[...Array(item.countInStock).keys()].map((x) => (
                                                    <option key={x + 1} value={x + 1}>
                                                        {x + 1}
                                                    </option>
                                                ))}
                                            </Form.Control>
                                        </Col>
                                        <Col md={2}>
                                            <Button
                                                type='button'
                                                variant='light'
                                                onClick={() => removeFromCartHandler(item.product)}
                                            >
                                                <i className='fas fa-trash'></i>
                                            </Button>
                                        </Col>
                                    </Row>
                                </ListGroup.Item>
                            ))}
                        </ListGroup>
                    </Col>
                    <Col md={4}>
                        <Card>
                            <ListGroup variant='flush'>
                                <ListGroup.Item>
                                    <h2>
                                        Subtotal ({cartItems.reduce((acc, item) => acc + item.qty, 0)})
                                        items
                                    </h2>
                                    â‚¹
                                    {cartItems
                                        .reduce((acc, item) => acc + item.qty * item.price, 0)
                                        .toFixed(2)}
                                </ListGroup.Item>
                                <ListGroup.Item>
                                    <div className="d-grid gap-2">
                                        <button
                                            onClick={checkoutHandler}
                                            className="btn btn-primary"
                                            type="button"
                                            disabled={cartItems.length === 0}
                                        >
                                            Proceed To Checkout
                                        </button>
                                    </div>
                                </ListGroup.Item>
                            </ListGroup>
                        </Card>
                    </Col>
                </Row>
            )}

        </Row >
    )
}

export default CartScreen
